import {
    LOGIN_IN,
    LOGIN_OUT,
    SELECT_FARM
} from './actionTypes';
import { getUserLocal, setUserLocal } from '@/config';

interface Action {
    type: string;
    payload?: object;
}
interface User {
    token: string,
    loginStatus: boolean,
    farmCode: string;
    farmName: string;
}

const { token = '', loginStatus = false, farmCode = '', farmName = '' }: User = getUserLocal();
const defaultState = () => ({ token, loginStatus, farmCode, farmName });
const emptyState = () => ({ token: '', loginStatus: false, farmCode: '', farmName: '' });
export default function(state: User = defaultState(), action: Action) {
    const { payload } = action;

    switch(action.type) {
        case LOGIN_IN: {
            setUserLocal(payload);
            return {
                ...state,
                ...payload
            }
        }
        case LOGIN_OUT: {
            setUserLocal(emptyState());
            return emptyState()
        }
        case SELECT_FARM: {
            setUserLocal(payload);
            return {
                ...state,
                ...payload
            }
        }
        default:
            return state
    }
}
