import React, { useState, useEffect } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { Box, Container } from '@material-ui/core';
import { Subscription } from 'rxjs';
import { map, finalize } from 'rxjs/operators';
import dayjs from 'dayjs';
import SwipeableViews from 'react-swipeable-views';
import clsx from 'clsx';
import { useTheme } from '@material-ui/core/styles';
import { fetchTodoTaskRuns$, getDraftsHz$ } from '@/api';
import AppBar from '@/components/app-bar';
import { TabPanel, Tabs, Item, Not3Item } from './components';
import useStyles from './styles';
import ClickLoadMore from '@/components/click-load-more';
// tab
//  pageMap 
const FirstFetchData: IndexMap<boolean> = {
    0: true,
    1: true,
    2: true,
    3: true
};
// 
// tabValuepageMap
// CachePageMappageMaptabValue
//  pageMap 
const CachePageMap: IndexMap<number> = {
    0: 1,
    1: 1,
    2: 1,
    3: 1,
};
const page_size = 5;
const tabList = ['', '', '', ''];
const Tasks: React.FC<RouteComponentProps> = props => {
    const classes = useStyles();
    const theme = useTheme();
    const [listMap, setListMap] = useState<IndexMap<any[]>>({
        0: [],
        1: [],
        2: [],
        3: []
    });
    const [pageMap, setPageMap] = useState<IndexMap<number>>({
        0: 1,
        1: 1,
        2: 1,
        3: 1,
    });
    const [nextMap, setNextMap] = useState<IndexMap<string>>({
        0: 'init',
        1: 'init',
        2: 'init',
        3: 'init',
    });
    const [loading, setLoading] = useState(false);
    const [tabValue, setTabValue] = useState(3);
    const dispatch = useDispatch();

    const  handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setTabValue(newValue);
    }
    const toTaskDetail2 = (item: any) => {
        item.done_status_label = item.done_status ? '' : '';
        dispatch({ type: 'SAVE_TASK_DETAIL2', payload: item });
        props.history.push(`/tasksDetail2/${item.id}`);
    }
    const toTaskDetail = () => props.history.push('/tasksDetail');

    const loadMore = (index: number) => ()=> {
        if (nextMap[index]) {
            const PageNum = pageMap[index] + 1;
            setPageMap({
                ...pageMap,
                [index]: PageNum
            });
        }
    };
    // 
    useEffect(() => {
        return () => {
            Object.keys(FirstFetchData).forEach(key => FirstFetchData[key] = true)
            Object.keys(CachePageMap).forEach(key => CachePageMap[key] = 1)
        }
    }, []);

    useEffect(() => {
        let ApiInstSub:  Subscription;
        const  finalOpera = (data: any) => {
            const [listMapItem, nextMapItem] = data;
            const hasData = Boolean(listMapItem && listMapItem.length);
            // 
            if (hasData) FirstFetchData[tabValue] = false;
            CachePageMap[tabValue] = pageMap[tabValue];
    
            hasData && setListMap(listMap => ({ ...listMap, [tabValue]: listMap[tabValue].concat(data[0]) }));
            setNextMap(nextMap => ({ ...nextMap, [tabValue]: nextMapItem }));
        }
    
        const getTask3List = () => {
            const ApiInst = fetchTodoTaskRuns$({ page: pageMap[3], page_size }).pipe(
                map(({response}) => response),
                map(response => [response.results, response.next]),
                map(([list, next]) => {
                    const tasksList =  list.map((item: any) => {
                        return {
                            ...item,
                            toNowDate: dayjs(item.run_date).diff(dayjs(), 'day')
                        }
                    });
                    return [tasksList, next];
                }),
                finalize(() => setLoading(false))
            ).subscribe(
                (data: any) => {
                    console.log('data', data);
                    finalOpera(data);
                }
            );
            return ApiInst;
        }
        const getTaskNot3List = () => {
            const UsageId: IndexMap<string> = {
                0: '270',
                1: '269',
                2: '268'
            };
            const Params = {
                page: pageMap[tabValue],
                page_size,
                usage_id: UsageId[tabValue]
            };
            const ApiInst = getDraftsHz$(Params).pipe(
                map(({response}) => response),
                map(response => Array.isArray(response) ? [response, null] : [response.results, response.next]),
                finalize(() => setLoading(false))
            ).subscribe(
                (data: any) => {
                    console.log('data', data);
                    finalOpera(data);
                }
            );
            return ApiInst;
        }

        if(FirstFetchData[tabValue] || CachePageMap[tabValue] !== pageMap[tabValue]){
            setLoading(true);
            ApiInstSub = tabValue === 3 ? getTask3List() : getTaskNot3List();
        }
        
        return () =>  {
            ApiInstSub && ApiInstSub.unsubscribe();
        };
    }, [tabValue, pageMap]);

    return (
        <Box className={clsx('bg-gray')}>
            <AppBar title="" />
            <Tabs value={tabValue} onChange={handleChange} tabList={tabList} />
            <Container>
                <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={tabValue}
                    onChangeIndex={(index) => setTabValue(index)}
                >
                    {
                        tabList.map((item, index) => {
                            return (
                                <TabPanel
                                    key={item}
                                    value={tabValue}
                                    index={index}
                                    dir={theme.direction}
                                >
                                    <Box className={classes.content}>
                                        <Choose>
                                            <When condition={index !== 3}>
                                                {
                                                    listMap[index].map((item: any, i) => (
                                                        <Not3Item 
                                                            key={`tasks-${index}item-${i}`}
                                                            {...item}
                                                            onClick={toTaskDetail}
                                                        />
                                                    ))
                                                }
                                            </When>
                                            <Otherwise>
                                                {
                                                    listMap[3].map((item: any, i) => (
                                                        <Item
                                                            key={`tasks-3-item-${i}`}
                                                            onClick={() => toTaskDetail2(item)}
                                                            {...item}
                                                        />
                                                    ))
                                                }
                                            </Otherwise>
                                        </Choose>
                                        <ClickLoadMore
                                            loading={loading}
                                            loadMore={loadMore(index)}
                                            hasMore={Boolean(nextMap[index])}
                                        />
                                    </Box>
                                </TabPanel>
                            )
                        })
                    }
                </SwipeableViews>
            </Container>
        </Box>
    );
}
export default Tasks;