const initState = JSON.parse(localStorage.getItem('taskDetail2') || '{}')

export default function(state = initState, action: Action) {
    const { payload, type} = action;

    switch (type) {
        case 'SAVE_TASK_DETAIL2': {
            const result =  {
                ...state,
                ...payload
            };
            setTimeout(() => {
                localStorage.setItem('taskDetail2', JSON.stringify(result));
            });
            return result;
        }
        case 'CLEAR_TASK_DETAIL2': {
            setTimeout(() => {
                localStorage.removeItem('taskDetail2');
            });
            return initState
        }
        default:
            return initState;
    }
}