import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { RouteComponentProps } from 'react-router-dom';
import { Box, Grid, Divider, FormControl, IconButton } from '@material-ui/core';
import { AttachFile } from '@material-ui/icons';
import {
    MuiPickersUtilsProvider,
    DatePicker,
    MaterialUiPickersDate
} from '@material-ui/pickers';
import LuxonUtils from "@date-io/luxon";
import { DateTime } from 'luxon';
import Dropzone from 'react-dropzone';
import { CloudUpload, InsertDriveFile } from '@material-ui/icons';
import useStyles from './styles';
import AppBar from '@/components/app-bar';
import TextAreaAndUploadImg from '@/components/textarea-upload-img';
import Button from '@/components/button';
import commonUpload from '@/hooks/useUpload';
import { todotaskruns$ } from '@/api';
import { zip, Subscription } from 'rxjs';
import { finalize, mergeMap } from 'rxjs/operators';
import { clone, ifElse, isNil, defaultTo, pipe, prop, join } from 'ramda';
import DoneState from '@/components/done-state';
import Viewer from 'viewerjs';
import 'viewerjs/dist/viewer.css';

interface DoneForm {
    done_date: null | MaterialUiPickersDate | DateTime;
    done_status: boolean;
    done_content: string;
    done_attachment: string;
    done_photos: string;
}
let gallery: any = null;

const TaskDetail2: React.FC<RouteComponentProps<{id: string}>> = props => {
    const ShareList = [
        {
            label: '',
            prop: 'todotask_name'
        },
        {
            label: '',
            prop: 'tasker_name'
        },
        {
            label: '',
            prop: 'location'
        },
        {
            label: '',
            prop: 'content'
        },
        {
            label: '',
            prop: 'target'
        }
    ];
    const DoneList = [
        {
            label: '',
            prop: 'done_date'
        },
        {
            label: '',
            prop: 'done_content'
        }
    ];
    const classes = useStyles();
    const detailInfo = useSelector((state: any) => state.TaskDetail2)
    const [doneForm, setDoneForm] = useState<DoneForm>({
        done_date: null,
        done_status: true,
        done_content: '',
        done_attachment: '',
        done_photos: ''
    });
    const [postLoading, setPostLoading] = React.useState(false);
    const [photo, setPhoto] = useState([]);
    const [files, setFiles] = useState<File[]>([]);
    const [viewImage, setViewImage] = useState(false);
    const dispatch = useDispatch();

    const handleDateChange = (date: null | MaterialUiPickersDate) => {
        setDoneForm({
            ...doneForm,
            done_date: date
        });
    };
    const getNoteAndImg = (data: any) => {
        if ('note' in data) {
            setDoneForm({
                ...doneForm,
                done_content: data.note
            });
        }
        if ('photo' in data) {
            setPhoto(data.photo);
        }
    };
    const handleSubmit = () => setPostLoading(true);
    const handleViewImage = () => setViewImage(true);

    useEffect(() => {
        let apiSub: Subscription;

        if (postLoading) {
            const uploadImgsObser = commonUpload({files: photo, keyWord: 'imgs'});
            const uploadFileObser = commonUpload({files: files, keyWord: 'files'});
            const params = clone(doneForm);

            apiSub = zip(
                uploadImgsObser,
                uploadFileObser
            ).pipe(
                mergeMap(val => {
                    const formatParam = (propName: string) => ifElse(
                        isNil,
                        defaultTo(''),
                        pipe(
                            prop(propName),
                            join(',')
                        )
                    );
                    const [imgs, files] = val.filter(item => typeof item === 'object');
                    

                    params.done_photos = formatParam('upload_imgs')(imgs);
                    params.done_attachment = formatParam('upload_files')(files);
                    params.done_date = ifElse(
                        isNil,
                        defaultTo(null),
                        (date: DateTime) => date.toISODate()
                    )(doneForm.done_date);
                    
                    return todotaskruns$({...params, id: props.match.params.id});
                }),
                finalize(() => setPostLoading(false))
            ).subscribe(
                () => {
                    dispatch({
                        type: 'SAVE_TASK_DETAIL2',
                        payload: {
                            done_status: true,
                            done_status_label: '',
                            ...params
                        }
                    });
                },
                error => console.error(error)
            );
        }
        return () => {
            apiSub && apiSub.unsubscribe();
        }
    }, [postLoading, doneForm, photo, files, dispatch, props.match.params.id]);
    
    useEffect(()=> {
        if (viewImage) {
            gallery.show();
        }
    }, [viewImage]);

    useEffect(() => {
        const elements = document.getElementById('images');
        gallery = elements && new Viewer(elements, {
            hidden: function () {
                setViewImage(() => false);
            }
        });
        return () => {
            gallery && gallery.destroy();
            gallery = null;
        };
    }, []);

    return (
        <Box className={classes.container}>
            <AppBar title="" />
            <DoneState
                done_state_label={detailInfo.done_status_label}
                done_state={detailInfo.done_status ? 1 : 2}
                className={classes.doneState}
            />
            {
                ShareList.map(item => (
                    <Box key={item.prop}>
                        <Grid container className={classes.listItem}>
                            <Grid item xs={3} >
                                {item.label}:
                            </Grid>
                            <Grid item xs={9}>
                                {detailInfo[item.prop] || ''}
                            </Grid>
                        </Grid>
                        <Divider />
                    </Box>
                ))
            }
            <Grid container className={classes.listItem}>
                <Grid item xs={3} >
                    :
                </Grid>
                <Grid item xs={9} >
                    <Choose>
                        <When condition={detailInfo.attachment}>
                            <Grid container spacing={1} >
                                {
                                    detailInfo.attachment.split(',').map((item: string, i: number) => (
                                        <Grid item xs={4} key={`attachment-${i}`} >
                                            <a href={item} download>
                                                <IconButton style={{padding: 0}}>
                                                    <AttachFile color="primary" />
                                                </IconButton>
                                            </a>
                                        </Grid>
                                    ))
                                }
                            </Grid>
                        </When>
                        <Otherwise>
                            
                        </Otherwise>
                    </Choose>
                </Grid>
            </Grid>
            <Divider />
            <Choose>
                <When condition={detailInfo.done_status}>
                    {
                        DoneList.map(item => (
                            <Box key={item.prop}>
                                <Grid container className={classes.listItem}>
                                    <Grid item xs={3}>
                                        {item.label}:
                                    </Grid>
                                    <Grid item xs={9}>
                                        {detailInfo[item.prop] || ''}
                                    </Grid>
                                </Grid>
                                <Divider />
                            </Box>
                        ))
                    }
                    
                    <Grid container className={classes.listItem}>
                        <Grid item xs={3} >
                            :
                        </Grid>
                        <Grid item xs={9} >
                            <Choose>
                                <When condition={detailInfo.done_attachment}>
                                    <Grid container spacing={1} >
                                        {
                                            detailInfo.done_attachment.split(',').map((item: string, i: number) => (
                                                <Grid item xs={6} key={`done-attachment-${i}`} >
                                                    <a href={item} download>
                                                        <IconButton style={{padding: 0}}>
                                                            <AttachFile color="primary" />
                                                        </IconButton>
                                                        <span>
                                                            { i + 1 }
                                                        </span>
                                                    </a>
                                                </Grid>
                                            ))
                                        }
                                    </Grid>
                                </When>
                                <Otherwise>
                                    
                                </Otherwise>
                            </Choose>
                        </Grid>
                    </Grid>
                    <Divider />
                    <Grid container className={classes.listItem}>
                        <Grid item xs={3} >
                            :
                        </Grid>
                        <Grid item xs={9} >
                            <Choose>
                                <When condition={detailInfo.done_photos}>
                                    <Grid container spacing={1} id="images" >
                                        
                                        {
                                            detailInfo.done_photos.split(',').map((item: string, i: number) => (
                                                <Grid item xs={4} key={`done-photo-${i}`} onClick={handleViewImage} >
                                                    <img src={item} className={classes.donePhoto} alt="" />
                                                </Grid>
                                            ))
                                        }
                                    </Grid>
                                </When>
                                <Otherwise>
                                    
                                </Otherwise>
                            </Choose>
                        </Grid>
                    </Grid>
                </When>
                <Otherwise>
                    <FormControl fullWidth margin="normal">
                        <MuiPickersUtilsProvider utils={LuxonUtils} locale="zh-cn">
                            <DatePicker
                                id="mui-pickers-date"
                                label=""
                                format="yyyy-MM-dd"
                                value={doneForm.done_date}
                                onChange={handleDateChange}
                                okLabel=""
                                cancelLabel=""
                                clearable
                                clearLabel=""
                            />
                        </MuiPickersUtilsProvider>
                    </FormControl>
                    <Box style={{paddingBottom: '10px'}}>
                        <TextAreaAndUploadImg
                            labelText=""
                            photo={photo}
                            note={doneForm.done_content}
                            saveReviewDetail={getNoteAndImg}
                            textAreaRequired={false}
                            uploadImgRequired={false}
                        />
                    </Box>
                    <Divider />
                    <Box style={{marginTop: '20px'}}>
                        <Dropzone onDrop={acceptedFiles => setFiles(acceptedFiles)}>
                            {({getRootProps, getInputProps}) => (
                                <section>
                                <div {...getRootProps()}>
                                    <input {...getInputProps()} />
                                    <Grid container  spacing={1}>
                                        <Grid item>
                                            <span>:</span>
                                        </Grid>
                                        <Grid item>
                                            <CloudUpload color="primary" />
                                        </Grid>
                                    </Grid>
                                </div>
                                </section>
                            )}
                        </Dropzone>
                    </Box>
                    <Box className="mt-5">
                        {
                            files.map((item: any) => (
                                <Grid container key={item.name} spacing={1}>
                                    <Grid item>
                                        <InsertDriveFile color="primary" />
                                    </Grid>
                                    <Grid item>
                                        {item.name}
                                    </Grid>
                                </Grid>
                            ))
                        }
                    </Box>
                    <If condition={files.length}>
                        <Divider />
                    </If>
                    <Button
                        btnText=""
                        onClick={handleSubmit}
                        loading={postLoading}
                        className={classes.btnMargin}
                    />
                </Otherwise>
            </Choose>
        </Box>
    )
}
export default TaskDetail2