import { uploadImgs$, uploadFiles$ } from '@/api';
import { of } from 'rxjs';
import { pluck } from 'rxjs/operators';

type KeyWord = 'imgs' | 'files'
interface Params {
    files: File[];
    keyWord: KeyWord;
}
export default function commonUpload(params:  Params) {
    const {keyWord, files} = params;
    if (!files.length) return of(keyWord);
    
    const Api$ = {
        'imgs': uploadImgs$,
        'files': uploadFiles$
    };
    const formData: FormData = new FormData();

    files.forEach((file: File) => formData.append(keyWord, file));

    return Api$[keyWord](formData).pipe(
        pluck('response')
    )
}
